
import { address } from "../utils/HttpRequest/request.js"
import { mapState } from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

import JsBarcode from 'jsbarcode'
import Wechat from './../utils/wechat'
export default {
components: {
Flexbox,
FlexboxItem,
Divider,
XHeader
},
data () {
return {
showMobile: '未绑定', // 在头部显示手机号或者未绑定
unbindOrupdate: '绑定手机号', //显示绑定手机号或者更换手机号
bindOrunbind: '未绑定',  //显示未绑定或者已绑定
name: '',   //名称
memberOrplus: '',   //普通会员或者超级会员
tel: '',  //判断有无手机号进行挑战，有跳入更换手机，无跳入绑定手机
showmonOrPLUS: true,
img: ''
}
},
methods: {

promotoCode () {
this.$router.push({path: '/promotoCode'});
},

bindMobile () {
this.$router.push({path: '/bindMobile',query:{type:7}});
},

addressList () {
this.$router.push({path: '/addresslist'});
},

encrytion(phone) {
var mphone = phone.substr(0, 3) + '****' + phone.substr(7);
return mphone;
},

createMember (barCode) {
JsBarcode("#barcode", barCode, {
width: 5,
height: 100,
fontSize: 50
})
}
},
created () {
Wechat.initHomeShare()
},
mounted () {
if (this.myInfo.member_code) {
this.createMember(this.myInfo.member_code)
} else {
this.$watch('myInfo', (newVal, old) => {
if (newVal.member_code) {
this.createMember(this.myInfo.member_code)
}
})
}
},
computed: {
...mapState({
myInfo: state => state.user.myInfo,
mySource: state => state.user.mySource,
memberCode: state => state.user.myInfo.member_code
})
}
}
